import axios from 'axios';
import React, { useEffect, useContext, useState } from 'react';
import { Badge, Fade, Card, CardBody, CardHeader, CardFooter, Col, Progress, Row, Table, Button } from 'reactstrap';
import { companyFinancial, companyInfo, companyReturn } from '../../api/symbol';
import CompanyAutoSuggest from '../../components/autocomplete/CompanyAutoSuggest';
import { format } from 'date-fns';
import { UserContext, MarketContext } from '../../context/contexts';
import firebase from '../../firebase/Firebase';
import Spinner from '../../components/spinner/Spinner';

function CompanyInfo(props) {
  const [companySymbol, setCompanySymbol] = useState('ADBL');
  const [fadeIn, setFadein] = useState(true);
  const [companyName, setCompanyName] = useState('Agriculture Development Bank Ltd.');
  const [trades, setTrades] = useState([]);
  const [financials, setFinancials] = useState([]);
  const [returns, setReturns] = useState();
  const [closeIndex, setCloseIndex] = useState({ lastClose: 0, previousClose: 0 });
  const [tradeOn, setTradeOn] = useState({ lastTradeOn: 0, previousTradeOn: 0 });
  const [volume, setVolume] = useState({ lastVolume: 0, previousVolume: 0 });
  const [token, setToken] = useState('anonymous');
  const [currentPE, setCurrentPE] = useState('N/A');

  const { uid } = useContext(UserContext);
  const { market } = useContext(MarketContext);


  const makeNetworkRequest = () => {
    axios.all([axios({ ...companyInfo, params: { ...companyInfo.params, symbol: companySymbol } }), axios({ ...companyFinancial, params: { ...companyFinancial.params, symbol: companySymbol } }), axios({ ...companyReturn, params: { ...companyReturn.params, symbol: companySymbol } })])
      .then(axios.spread((companyInfo, companyFinancial, companyReturn) => {
        setCloseIndex({ lastClose: companyInfo.data.lastClose, previousClose: companyInfo.data.previousClose });
        setTradeOn({ lastTradeOn: companyInfo.data.lastTradeOn, previousTradeOn: companyInfo.data.previousTradeOn });
        setVolume({ lastVolume: companyInfo.data.lastVolume, previousVolume: companyInfo.data.previousVolume });
        setTrades(companyInfo.data.trades);
        setFinancials(companyFinancial.data);
        setReturns(companyReturn.data);
      }))
  }

  const updateSymbol = (symbol, name) => {
    setCompanySymbol(symbol);
    setCompanyName(name);
  }


  useEffect(() => {
    makeNetworkRequest();
    if (market == "Live") {
      const intervalId = setInterval(makeNetworkRequest, 60000);
      return () => {
        clearInterval(intervalId);
      }
    }
  }, [companySymbol]);


  useEffect(() => {
    if (uid) {
      firebase.auth().currentUser.getIdToken(true)
        .then((idToken) => {
          setToken(idToken);
        })
    } else {
      setToken(null);
    }
  }, [uid]);

  useEffect(() => {
    if (market == "Live") {
      let toggleId = -1;
      toggleId = setInterval(() => {
        setFadein(!fadeIn)
      }, 600);
      return () => {
        clearInterval(toggleId);
      }
    } else {
      setFadein(true);
    }
  });

  useEffect(() => {
    if (financials.length != 0) {
      const latestQuarter = financials.reduce((max, current) => current.QUARTER > max ? current.QUARTER : max, financials[0].QUARTER);
      const latestFinancial = financials.filter(financial => financial.QUARTER == latestQuarter)[0];
      setCurrentPE((closeIndex.lastClose / latestFinancial.EPS).toFixed(2));
    }
  }, [financials, closeIndex])




  const weeklyHighLow = trades.map(trade => {
    let closeRange;
    closeRange = 100 * ((closeIndex.lastClose - trade.LOWINDEX) / (trade.HIGHINDEX - trade.LOWINDEX));
    return (
      <div key={trade.RANGETYPE} className="progress-group mb-4">
        <div className="progress-group-prepend">
          <span className="progress-group-text">
            {trade.RANGETYPE}
          </span>
        </div>
        <div className="progress-group-bars">
          <div className="clearfix">
            <div className="float-left">
              <strong>{trade.LOWINDEX}</strong>
              <small className="text-muted"> Low</small>
            </div>
            <div className="float-right">
              <small className="text-muted">High </small>
              <strong>{trade.HIGHINDEX}</strong>
            </div>
          </div>
          <Progress className="progress-xs" color={closeRange >= 50 ? "success" : "danger"} value={closeRange} />
        </div>
      </div>
    )

  });

  const financialTable = financials.map(financial => {
    return (
      <tr key={financial.FISCALYEAR + financial.QUARTER}>
        <td>
          <div className="small text-muted">{financial.FISCALYEAR}/{financial.FISCALYEAR + 1}</div>
          <strong>Quarter{financial.QUARTER}</strong>
        </td>
        <td>
          <div>{financial.EPS}</div>
        </td>
        <td>
          <div>{financial.PERATIO}</div>
        </td>
        <td>
          <div>{financial.NETWORTH}</div>
        </td>
        <td className="d-none d-sm-table-cell">
          {financial.REPORTFILE ? <Button className="bg-success" size="sm" onClick={() => window.open((!document.URL.match(/^http?:/) && financial.REPORTFILE.substr(((financial.REPORTFILE).lastIndexOf('.') + 1)) == 'pdf' ? "https://docs.google.com/viewer?embedded=true&url=" : '') + "https://files.nepsechart.com/reports/" + financial.REPORTFILE, "_blank", 'location=no')} >Report</Button> : <small className="text-muted">Not Available</small>}
        </td>
      </tr>
    )
  })

  const StockDirectionIcon = () => {
    if (closeIndex.lastClose > closeIndex.previousClose) {
      return <Fade in={fadeIn}><Badge color="success"><i className="fa fa-arrow-up" /></Badge></Fade>
    } else if (closeIndex.lastClose < closeIndex.previousClose) {
      return <Fade in={fadeIn}><Badge color="danger"><i className="fa fa-arrow-down" /></Badge></Fade>
    } else {
      return <Fade in={fadeIn}><Badge color="primary"><i className="fa fa-arrows-h" /></Badge></Fade >
    }
  }

  const redirectToCharts = () => {
    if (!token) {
      props.history.push('/login');
    } else {
      window.open('https://chart.nepsechart.com?symbol=' + companySymbol + '&token=' + token, '_blank', 'location=no');
    }
  }



  return (
    <div className="animated fadeIn">
      <Row>
        <Col className="px-0">
          <Card>
            <CardHeader className="bg-info">
              <Row>
                <Col xs="10">
                  <div className="d-inline-block">
                    <strong className="pr-2">{companyName + '    '}</strong>
                  </div>
                  <div className="d-inline-block">
                    <StockDirectionIcon />
                  </div>
                </Col>
                <Col xs="2">
                  <Button className="float-right bg-warning" size="sm" onClick={redirectToCharts} >Chart</Button>
                </Col>
              </Row>
            </CardHeader>
            <CardBody>
              <Row>
                <Col lg="4" sm="6" xs="12">
                  <div className="callout callout-info">
                    <CompanyAutoSuggest onSuggestionSelected={updateSymbol} />
                    <small className="text-muted">Type Name or Symbol</small>
                  </div>
                </Col>
                <Col lg="2" sm="3" xs="12">
                  <div className="callout callout-info">
                    <small className="text-muted">Current Rate</small>
                    <br />
                    <strong className="h4">{closeIndex.lastClose ? (closeIndex.lastClose).toLocaleString() : 'N/A'} | </strong>
                    <strong className={closeIndex.lastClose >= closeIndex.previousClose ? "text-success" : "text-danger"}>{closeIndex.lastClose - closeIndex.previousClose} </strong>
                    <small className={closeIndex.lastClose >= closeIndex.previousClose ? "text-success" : "text-danger"}> ( {(((closeIndex.lastClose - closeIndex.previousClose) / closeIndex.previousClose) * 100).toFixed(2)}% ) </small>                      </div>
                </Col>
                <Col lg="2" sm="3" xs="12">
                  <div className="callout callout-info">
                    <small className="text-muted">Current P/E</small>
                    <br />
                    <strong className="h4">{currentPE}</strong>
                  </div>
                </Col>
                <Col lg="2" sm="6" xs="12">
                  <div className="callout callout-info">
                    <small className="text-muted">Volume</small>
                    <br />
                    <strong className="h4">{volume.lastVolume ? (volume.lastVolume).toLocaleString() : 'N/A'}</strong>
                  </div>
                </Col>
                <Col lg="2" sm="6" xs="12">
                  <div className="callout callout-info">
                    <small className="text-muted">Last Trade On</small>
                    <br />
                    <strong className="h4">{tradeOn.lastTradeOn ? format(new Date(tradeOn.lastTradeOn * 1000), 'D-MMM-YYYY') : 'N/A'}</strong>
                  </div>
                </Col>
              </Row>
              <hr className="mt-0" />
              {weeklyHighLow}



            </CardBody>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col className="px-0">
          <Card>
            <CardHeader className="bg-info">
              Latest Returns and Fundamentals
              </CardHeader>
            <CardBody>
              {returns ?
                <Row>
                  <Col xs="12" md="12" xl="12">
                    <Row>
                      <Col sm="4">
                        <div className="callout callout-info">
                          <strong>Bonus</strong><br />
                          <strong className="h4">{returns.hasOwnProperty('bonus') ? returns.bonus.percentage : "No Bonus"}</strong>
                          <small className="text-muted">{returns.hasOwnProperty('bonus') ? returns.bonus.fiscalYear + '/' + (returns.bonus.fiscalYear + 1) : null}</small>
                        </div>
                      </Col>
                      <Col sm="4">
                        <div className="callout callout-info">
                          <small className="text-muted">Cash</small>
                          <br />
                          <strong className="h4">{returns.hasOwnProperty('cash') ? returns.cash.percentage : 'No Cash'}</strong>
                          <small className="text-muted">{returns.hasOwnProperty('cash') ? returns.cash.fiscalYear + '/' + (returns.cash.fiscalYear + 1) : null}</small>
                        </div>
                      </Col>
                      <Col sm="4">
                        <div className="callout callout-danger">
                          <small className="text-muted">Rights</small>
                          <br />
                          <strong className="h4">{returns.hasOwnProperty('right') ? returns.right.percentage : "No Right"}</strong>
                          <small className="text-muted">{returns.hasOwnProperty('right') ? returns.right.fiscalYear + '/' + (returns.right.fiscalYear + 1) : null}</small>
                        </div>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                : null}
              <br />
              <Table hover responsive className="table-outline mb-0">
                <thead className="bg-primary">
                  <tr>
                    <th>Quarter</th>
                    <th>EPS</th>
                    <th>P/E Ratio</th>
                    <th>Net Worth</th>
                    <th className="d-none d-sm-table-cell">Report</th>
                  </tr>
                </thead>
                <tbody>
                  {financialTable}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div >
  );
}


export default CompanyInfo;
